import { useForm, Controller } from "react-hook-form";
import React, { useState, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../../actions/auth";
import { Navigate, useNavigate } from "react-router-dom";
import { Modal } from "bootstrap";
import { getData, postData } from "../../services/user.service";
import { toast } from "react-toastify";
import Cookies from "js-cookie";
let emID = Cookies.get("empid");
const ChangepassHome = (props) => {
  const [message, setmessage] = useState("");
  const [modal, setModal] = useState(null);
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    control,
    formState: { errors },
  } = useForm();

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (props.clear === true) {
      reset();
    }
  }, [props.clear]);
  const hideModal = () => {
    reset();
    modal.hide();
  };

  const validateForm = {
    Password: {
      required: "Khng c  trng",
      maxLength: {
        value: 15,
        message: " khng c hn 10 k t",
      },
    },
    passwordNew: {
      required: "Khng c  trng",
      minLength: {
        value: 6,
        message: " khng c t hn 6 k t",
      },
    },
    passwordNewVerify: {
      required: "Khng c  trng",
      minLength: {
        value: 6,
        message: " khng c t hn 6 k t",
      },
    },
  };

  const onChagepassOK = async (data) => {
    console.log(data);
    if (data.passwordNew !== data.passwordNewVerify) {
      setmessage(" Mt khu xc nhn khng khp");
    } else {
      setmessage("");
      //console.log("first");
      var create = await postData("changePass", {
        username: emID,
        currentPassword: data.Password,
        newPassword: data.passwordNew,
        confirmPass: data.passwordNewVerify,
      });
      console.log(create);
      if (create.isSuccess === 1) {
        toast.success("i mt khu thnh cng \n" + create.note, {
          autoClose: 2000,
          className: "",
          position: "top-center",
          theme: "colored",
        });
        setValue("Password", data.passwordNew);
        console.log(data.MSNV);
        Cookies.set("empid", data.MSNV);
        reset();
        hideModal();
      } else {
        if (create.note) {
          toast.error("i mt khu tht bi Li: \n" + create.note, {
            autoClose: 2000,
            className: "",
            position: "top-center",
            theme: "colored",
          });
        } else {
          toast.error("Mt khu c sai \n", {
            autoClose: 2000,
            className: "",
            position: "top-center",
            theme: "colored",
          });
        }
      }
    }
  };
  return (
    <>
      <form>
        <div className="row d-flex justify-content-center align-items-center h-100">
          <div className="">
            <div className="text-center">
              <h3>I MT KHU </h3>
              <div className="dropdown-divider" />
              <div className="form-outline mb-1">
                <label className="form-label" htmlFor="password">
                  Mt khu C
                </label>
                <div className="d-flex">
                  <input
                    className="form-control"
                    id="Password"
                    {...register("Password", validateForm.Password)}
                  />
                </div>
              </div>

              <div className="form-outline mb-1">
                <label className="form-label" htmlFor="password">
                  Mt khu mi
                </label>
                <div className="d-flex">
                  <input
                    type="password"
                    className="form-control"
                    id="passwordNew"
                    {...register("passwordNew", validateForm.passwordNew)}
                  />
                </div>
                <p
                  style={{
                    fontSize: "10px",
                    color: "red",
                  }}
                >
                  {errors.passwordNew?.message}
                </p>
              </div>
              <div className="form-outline mb-1">
                <label className="form-label" htmlFor="password">
                  Nhp li mt khu mi
                </label>
                <div className="d-flex">
                  <input
                    type="password"
                    className="form-control"
                    id="passwordNewVerify"
                    {...register(
                      "passwordNewVerify",
                      validateForm.passwordNewVerify
                    )}
                  />
                </div>
                <p
                  style={{
                    fontSize: "10px",
                    color: "red",
                  }}
                >
                  {errors.passwordNewVerify?.message}
                </p>
                {message && (
                  <div className="form-group">
                    <div
                      className="alert alert-danger text-center"
                      role="alert"
                    >
                      {message}
                    </div>
                  </div>
                )}
              </div>
              <div className="form-group">
                <button
                  className="btn btn-primary btn-block fa-lg gradient-custom-2 mb-3"
                  type="submit"
                  disabled={loading}
                  onClick={handleSubmit(onChagepassOK)}
                >
                  {loading && (
                    <span className="spinner-border spinner-border-sm"></span>
                  )}
                  <span> Thay i</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    </>
  );
};
export default ChangepassHome;
